@import url(https://fonts.googleapis.com/css?family=Nunito:600,700&display=swap);
body {
  margin: 0;
  font-family: Nunito, Roboto, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(#608FDA, #A5B6F0);
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div{
  justify-content: center;
}

.container{
  text-align: center;
  margin-top: 100px;
}

.container header{
  font-size: 1.3rem;
  margin: 0 auto 60px auto;
  position: relative;
  height: 25px;
  width: 250px;
  overflow: hidden;
}

.container p {
  font-size: 12px;
  margin-top: 50px;
}

button {
  border: 10px;
  padding: 10px;
  font-size: 20px;
  color: white;
  background-color: rgba(255,255,255,0.3);
  border-radius: 5px;
}

.simpleResult {
  display:flex;
  height: 100%;
  padding: 20px;
}

.simpleResult h2{
  color: #ffff;
  padding: 20px;
  margin: 20px 10px;
  background: rgba(255,255,255,0.2);
  border-radius: 100px;
}

.simpleResult p{
  color: #ffff;
}

.simpleResult button{
  font-size: 14px;
}

.detailedResult{
  background: rgba(255,255,255,0.1);
}

.detailedResult p{
  display: flex;
  color: #ffff;
  width: 100px;
  margin-top: 10px;
}

.detailedResult .individualResult {
  padding: 20px;
  justify-content: center;
  /* background: rgba(255,255,255,0.3); */
  display: flex;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button_button__3Pz0r {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}

.button_button__3Pz0r:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
